import { html } from "lit-html";
import * as webs from "@webs/component-library";
import { styleMap } from "lit-html/directives/style-map";
import "./button.css";

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  primary,
  backgroundColor = null,
  size,
  label,
  onClick,
}) => {
  const mode = primary
    ? "storybook-button--primary"
    : "storybook-button--secondary";

  const webs_button = new webs.Button();

  return html`
    <webs-button
      text="{{ module.title }}"
      extraClasses="{{ module.button_color }}"
      href="{{ module.link.url.href }}"
      openInNewTab="{{ module.link.open_in_new_tab }}"
    ></webs-button>
  `;
};
